* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Avenir Next";
  min-width: 1280px;
}

ul {
  list-style: none;
}

ol.table-of-contents,
ol.table-of-contents ol{
  counter-reset: list;
  list-style: none;
  padding: 0;
  width:350px;
  font: 14px/1.6 'arial narrow', sans-serif;
}

ol.table-of-contents ol{
  margin: 0 0 0 35px;
  width:315px;
}

.table-of-contents a{
  position: relative;
  display: block;
  text-decoration: none;
  padding: 3px 3px 3px 8px;
  margin: 8px 0 8px 30px;
  background: #f1f8ff;
  color: #333;
  font-weight: bold;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  -ms-transition: all .3s ease-out;
  -o-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.table-of-contents a:hover{
  background: #ffd9d3;
}

.table-of-contents a:before{
  content: counter(list);
  counter-increment: list;
  position: absolute;
  left: -35px;
  top: 50%;
  height: 28px;
  width: 28px;
  margin-top: -1em;
  background: #5c9ee7;
  line-height: 28px;
  text-align: center;
  font-weight: bold;
  color: #fff;
}

.table-of-contents a:after{
  content: '';
  position: absolute;
  left: -28px;
  top: 50%;
  margin-top: -6px;
  border: 6px solid transparent;
  -webkit-transition: all .3s ease-out;
  -moz-transition: all .3s ease-out;
  -ms-transition: all .3s ease-out;
  -o-transition: all .3s ease-out;
  transition: all .3s ease-out;
}

.table-of-contents a:hover:after{
  left: -6px;
  border-left-color: #5c9ee7;
}

h1 {
  font-family: "Avenir Next";
  font-size: 56px;
  font-weight: 400;
  color: #0bc8b6;
}

h2, h3 {
  font-family: "Lato";
  color: #2b546a;
  font-weight: 300;
}

h2 {
  font-size: 24px;
  margin-top: 8px;
}

h3 {
  font-size: 20px;
  border-bottom: 1px solid #e1e6ec;
  line-height: 64px;
  margin-bottom: 40px;
}

.header {
  height: 60px;
  border-bottom: 1px solid #eee;
}

.header-logo {
  height: 60px;
  width: 60px;
  background-color: #2b546a;
  float: left;
  text-align: center;
}

.header-logo img {
  width: 44px;
  padding-top: 8px;
}

.main-wrapper {
  background-color: #f4fafa;
}

.main {
  max-width: 1040px;
  margin: 0 auto;
  padding: 96px 0;
}

.component-container {
  margin-top: 96px;
}

.school-days-container {
  margin-top: 96px;
}

.main .picture img {
  width: 100%;
  margin-bottom: 54px;
}

.main .picture .sub-picture img {
  width: 50%;
  margin-bottom: 54px;
  padding: 0 10px;
}

.hobby-container {
  margin-top: 96px;
}

.component-card {
  display: inline-block;
  width: 22.5%;
  margin-right: 24px;
  margin-bottom: 24px;
  text-align: center;
  box-shadow: 0 0 6px #e8e8e8;
  background-color: #fff;
  cursor: pointer;
}

.component-item {
  border-radius: 4px;
  padding: 48px 0 64px;
}

.component-item img {
  width: 126px;
  height: 126px;
}

.component-item p {
  color: #2b546a;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal-inner {
  position: absolute;
  top: 8%;
  right: 0;
  left: 0;
  width: 480px;
  padding-bottom: 60px;
  margin: auto;
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
}

.modal-header {
  height: 8px;
  width: 100%;
  margin-bottom: 60px;
  border-radius: 8px 8px 0 0;
  background-image: url("modal_bar.svg");
}

.modal-introduction p {
  color: #5876a3;
  width: 384px;
  line-height: 32px;
  text-align: left;
  margin: 36px auto 40px;
}

.modal-close-btn {
  font-size: 13px;
  color: #8491a5;
  width: 200px;
  padding: 16px 0;
  border: 0;
  background-color: #f0f4f9;
  cursor: pointer;
}

.modal-close-btn:hover {
  color: #8491a5;
  background-color: #ccd9ea;
  transition: .3s ease-in-out;
}

.footer {
  padding: 50px 0;
}

.footer-inner {
  max-width: 1040px;
  margin: 0 auto;
}

.footer-logo {
  display: inline-block;
}

.footer-logo img {
  height: 20px;
}

.footer-logo p {
  color: #b1c0c0;
  font-size: 12px;
}

.footer-inner ul {
  float: right;
}

.footer-inner li {
  color: #2b546a;
  display: inline-block;
  padding-left: 16px;
}
